import React, { useState } from 'react'
import './App.css'
import { useQuery } from '@apollo/client'
import { ALL_TOPICS } from './queries/queries'
import loadingAnimation from './resources/loading.gif'
import Topics from './components/Topics'
import Navbar from './components/Navbar'

const App = () => {

  const [body, setBody] = useState('')

  const result = useQuery(ALL_TOPICS)

  if (result.loading) {
    return (
      <div>
        <img src={loadingAnimation} alt='loading animation' />
        Loading...
      </div>
    )
  }

  if (body === 'main') {
    return (
      <div className="App">
        <div>
          <Navbar setBody={setBody} />
          <h4>Aiheet</h4>
          <Topics topics={result.data.allTopics} />
        </div>
      </div>
    )
  }

  if (body === 'covered') {
    return (
      <div>
        <Navbar setBody={setBody} />
        <h4>Vanhat Aiheet</h4>
      </div>
    )
  }
}

export default App
